import Axios from 'axios'
import Mock from 'mockjs'
import mockData from './mock.js'
// 是否mock
const isMock = false

const domain = ''

// 设置mock延迟时间
Mock.setup({
  timeout: '200-400'
})

export default {
  // 是否登录
  checkLogin (data, type = 'get') {
    let url = domain ? `${domain}/api/admin/iflogin.json` : '/api/admin/iflogin.json'
    if (isMock) {
      Mock.mock(url, mockData.checkLogin)
    }
    return Axios[type](url, data)
  },
  // 登录请求
  login (data, type = 'get') {
    let url = domain ? `${domain}/api/admin/login.json` : '/api/admin/login.json'
    if (isMock) {
      Mock.mock(url, mockData.login)
    }
    return Axios[type](url, data)
  },
  // 资讯列表
  informationList (data, type = 'get') {
    let url = domain ? `${domain}/api/admin/informations.json` : '/api/admin/informations.json'
    if (isMock) {
      Mock.mock(url, mockData.informationList)
    }
    return Axios[type](url, data)
  },
  // 删除资讯
  deteleInfomation (data, type = 'get') {
    type = 'delete'
    let url = domain ? `${domain}/api/admin/informations/${data.information_id}.json` : '/api/admin/informations/' + data.information_id + '.json'
    if (isMock) {
      Mock.mock(url, mockData.deteleInfomation)
    }
    return Axios[type](url, data)
  },
  // 资讯详情
  InfomationDetail (data, type = 'get') {
    let url = domain ? `${domain}/api/admin/informations/${data.information_id}.json'` : '/api/admin/informations/' + data.information_id + '.json'
    if (isMock) {
      Mock.mock(url, mockData.InfomationDetail)
    }
    return Axios[type](url, data)
  },
  // 新增资讯
  addInformation (data, type = 'get') {
    let url = domain ? `${domain}'/api/admin/informations.json'` : '/api/admin/informations.json'
    if (isMock) {
      Mock.mock(url, mockData.addInformation)
    }
    return Axios[type](url, data)
  },
  // 编辑资讯
  editInformation (data, type = 'get') {
    let url = domain ? `${domain}'/api/admin/informations/${data.information_id}.json'` : '/api/admin/informations/' + data.information_id + '.json'
    if (isMock) {
      Mock.mock(url, mockData.editInformation)
    }
    return Axios[type](url, data)
  },
  // 自定义页面列表
  pageList (data, type = 'get') {
    let url = domain ? `${domain}'/api/admin/pages.json'` : '/api/admin/pages.json'
    if (isMock) {
      Mock.mock(url, mockData.pageList)
    }
    return Axios[type](url, data)
  },
  // 自定义页面详情
  pageDetail (data, type = 'get') {
    let url = domain ? `${domain}'/api/admin/pages/${data.page_id}.json'` : '/api/admin/pages/' + data.page_id + '.json'
    if (isMock) {
      Mock.mock(url, mockData.pageDetail)
    }
    return Axios[type](url, data)
  },
  // 新增自定义页面
  addPage (data, type = 'get') {
    type = 'post'
    let url = domain ? `${domain}'/api/admin/pages.json'` : '/api/admin/pages.json'
    if (isMock) {
      Mock.mock(url, mockData.addPage)
    }
    return Axios[type](url, data)
  },
  // 编辑自定义页面
  editPage (data, type = 'get') {
    type = 'patch'
    let url = domain ? `${domain}'/api/admin/pages${data.page_id}.json'` : '/api/admin/pages/' + data.page_id + '.json'
    if (isMock) {
      Mock.mock(url, mockData.editPage)
    }
    return Axios[type](url, data)
  },
  // 删除资讯
  detelePage (data, type = 'get') {
    type = 'delete'
    let url = domain ? `${domain}'/api/admin/pages${data.page_id}.json'` : '/api/admin/pages/' + data.page_id + '.json'
    if (isMock) {
      Mock.mock(url, mockData.detelePage)
    }
    return Axios[type](url, data)
  }
}
